import createMuiTheme from 'material-ui/styles/createMuiTheme';
import indigo from 'material-ui/colors/indigo';
import pink from 'material-ui/colors/pink';

export default createMuiTheme({
  palette: {
    primary: indigo,
    secondary: pink,
    type: 'light'
  }
});



